.banner-img {
	margin-top: 60px;
}

.home-page h1 {
	color: gray;
	font-family: "Playfair Display", serif;
}

.home-page {
	display: flex;
	flex-wrap: wrap;
}

.home-page .card {
	background-color: rgba(128, 128, 128, 0.097);
	width: 18rem;
}

.home-page .card-name-price {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.home-page .card-price {
	color: green;
	font-weight: bold;
}

.home-page button {
	border-radius: 0;
	width: 100%;
	border-top-left-radius: 5px;
	border-bottom-right-radius: 5px;
	font-size: 14px;
	font-weight: bold;
}

.home-page .card-text {
	color: rgb(90, 89, 89);
}

.home-page .card img:hover {
	transform: scale(0.9);
}

.home-page .card-img-top {
	height: 300px !important;
}

/* ==================
filters
============== */
.filters h4 {
	margin-top: 100px;
	color: gray !important;
	border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}

.ant-checkbox-wrapper:first-of-type {
	margin-left: 8px;
}

.ant-checkbox-wrapper {
	margin: 2px;
}

.ant-radio-wrapper {
	margin-bottom: 5px !important;
	margin-left: 10px;
}

.filters button {
	background-color: black;
	width: 100%;
	border: none;
	border-radius: 0;
	margin-top: 20px;
}

.loadmore {
	color: green;
	font-weight: bold;
	font-size: 20px !important;
}

.custom-button {
	background-color: #4D4D50;
	/* Add any other desired styles */
	color: white;
}

.center-caption{
	margin-bottom: -2.2rem;
}
.glowing-h1 {
	text-shadow: 0 0 5px rgb(33 33 33 / 80%),
		0 0 10px rgb(0 0 0 / 0%),
		0 0 15px rgb(0 0 0 / 13%)
}